.container {
  width: 1140px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
.container .products {
  min-height: calc(100vh - 80px);
  background-color: rgba(221, 221, 221, 0.5294117647);
  display: grid;
  grid-template-columns: repeat(4, minmax(250px, 1fr));
  gap: 20px;
}/*# sourceMappingURL=Products.css.map */